// @flow

import _ from "underscore";

// Things we need to check:
// - Element has `position: sticky`
// - Element has an offset, e.g. `top: 0px`
// - Element is smaller than its parent in the scrolling direction
// - No ancestor elements have overflow-x/y "hidden"

// Takes the element we want to be sticky and returns advice
// for why it might not be working.
export default function stickyDebugger(stickyElement: HTMLElement) {
  const stickyElementComputedStyle = window.getComputedStyle(stickyElement);
  const stickyElementPosition = stickyElementComputedStyle["position"];
  if (stickyElementPosition !== "sticky") {
    console.warn(
      `Sticky element has non-sticky position '${stickyElementPosition}' so it won't "float"`,
      stickyElement
    );
  }

  const stickyElementOffset = {
    top: stickyElementComputedStyle["top"],
    left: stickyElementComputedStyle["left"],
    bottom: stickyElementComputedStyle["bottom"],
    right: stickyElementComputedStyle["right"]
  };
  const nonAutos = _.pick(stickyElementOffset, value => value !== "auto");
  if (Object.values(nonAutos).length === 0) {
    console.warn(
      'Sticky element has no explicit offset (e.g. top: 0px) so it won\'t "float"',
      stickyElement
    );
  } else {
    console.info(
      `Sticky element should "float" by sticking to the ${Object.keys(
        nonAutos
      ).join("/")} side of its parent element when that's scrolled`,
      stickyElement
    );
  }

  const stickyElementParent = stickyElement.parentElement;
  if (!stickyElementParent) {
    console.warn(
      "Sticky element has no parent so it will never float",
      stickyElement
    );
  } else {
    const stickyElementHeight = stickyElement.offsetHeight;
    if (stickyElementHeight >= stickyElementParent.clientHeight) {
      console.warn(
        "Sticky element is the same height as its parent so it won't be able to move",
        stickyElement
      );
    }
  }

  let currentElement = stickyElement.parentElement;
  while (currentElement) {
    const scrollContainerMessage = getScrollContainerMessage(currentElement);
    if (scrollContainerMessage.length > 0) {
      console.warn(...scrollContainerMessage);
    }

    currentElement = currentElement.parentElement;
  }
}

function getScrollContainerMessage(element: Element): Array<any> {
  const computedStyle = window.getComputedStyle(element);
  const overflowX = computedStyle["overflow-x"];
  const overflowY = computedStyle["overflow-y"];

  const isOverflowX = overflowCreatesScrollContainer(overflowX);
  const isOverflowY = overflowCreatesScrollContainer(overflowY);

  if (!isOverflowX && !isOverflowY) {
    return [];
  }

  if (overflowX === overflowY) {
    return [
      `Ancestor is scroll container because of overflow: ${overflowX}`,
      element
    ];
  }

  const reasons = _.compact([
    isOverflowX && `overflow-x: ${overflowX}`,
    isOverflowY && `overflow-y: ${overflowY}`
  ]);

  return [`Ancestor is scroll container because of ${reasons}`, element];
}

function overflowCreatesScrollContainer(overflow: string): boolean {
  switch (overflow) {
    case "visible":
    case "clip":
    case "-moz-hidden-unscrollable":
      return false;
    case "auto":
    case "hidden":
    case "scroll":
      return true;
    default:
      return false;
  }
}
